<template>
<li class="scientific">
    <div class="icon scientific"></div>
    <div :class="['status', scientific.status]" v-if="showStatus">
        <div v-if="scientific.status === 'project'">
            <p>Projet</p>
            <span class="time">{{getProjectTimeLeft(scientific)}}</span>
        </div>
        <div v-else-if="scientific.status === 'projectFinish'" @click="endProject()">
            <p>Projet terminé</p>
        </div>
        <div v-else>
            <p>
            <router-link :to="'project/'+indexScientific">Nouveau projet</router-link>
            </p>
        </div>
    </div>
    <span class="name">Équipe {{getScientificName(indexScientific)}}</span>
    <p class="level">Niveau de compétence: {{scientific.level}}</p>
</li>
</template>

<script>
import { alphabetGrec, formatTime } from "@/labels";
import { state } from "@/game";
import { endProject } from "@/projects";

export default {
  name: "Scientific",
  props: {
    showStatus: { type: Boolean, default: false },
    indexScientific: Number
  },
  data() {
    return {
      t: Date.now()
    };
  },
  computed: {
    scientific() {
      return state.scientifics[this.indexScientific];
    }
  },
  methods: {
    getScientificName: i => alphabetGrec[i],
    formatTime: formatTime,
    getProjectTimeLeft(scientific) {
      if (!scientific.projectEndTime) return "";
      let timeLeft = scientific.projectEndTime - this.t;
      if (timeLeft <= 0) {
        scientific.status = "projectFinish";
        delete scientific.projectEndTime;
      }
      return formatTime(timeLeft);
    },

    endProject() {
      endProject(this.scientific);
      this.$router.push("/project-resume/" + this.indexScientific);
    }
  },

  created() {
    this.tInterval = setInterval(() => {
      this.t = Date.now();
    }, 1000);
  },

  destroyed() {
    clearInterval(this.tInterval);
  }
};
</script>

