<template>
    <div id="project" v-if="scientific">
        <ul>
            <Scientific :indexScientific="indexScientific" />
        </ul>
        <h1>Projets de recherche</h1>
        <ul>
            <li v-for="(project,i) in projects" :key="i"
                v-if="!selectedProject || selectedProject === project"
                :class="['project', { done: isProjectDone(project) }]"
                @click="selectProject(project)">
                <span class="name">{{project.name}}</span>
                <p class="description" v-if="selectedProject === project">{{project.description}}</p>

                <ul class="gains">
                    <li v-for="(gain, i) in project.gains" :key="i">
                        <p>{{gain.description}}</p>
                    </li>
                </ul>
                <p :class="['level', { insufficient: project.level > scientific.level }]">
                  Niveau de compétence requis:
                  <span>{{project.level}}</span>
                </p>
            </li>
        </ul>
        <template v-if="selectedProject">
        <h1>Durée du projet de recherche</h1>
        <div class="params">
            <span class="duree">{{dureeProject}} h</span>

            <p class="ressources">Ressources requises:
                <ul>
                    <li v-for="(val,key) in selectedProject.ressources" :key="key"
                        :class="{ insufficient: !hasRessources({ [key]: val }) }">
                        <template v-if="val > 0">
                          {{val}} <div :class="['icon', key]"></div>
                        </template>
                    </li>
                </ul>
            </p>
        </div>

        <div class="action disabled" v-if="!hasRessources(requiredRessources)">
          <a>Ressources insuffisantes</a>
        </div>
        <div class="action disabled" v-else-if="scientific.level < selectedProject.level">
          <a>Niveau insuffisant</a>
        </div>
        <div class="action" v-else-if="canStartProject">
            <a @click="startProject()">Démarrer le projet</a>
        </div>
        </template>
    </div>
</template>

<script>
import Scientific from "@/components/Scientific.vue";
import { projects, startProject, calcRequiredRessources } from "@/projects.js";
import { hasRessources, takeRessources } from "@/ressources.js";
import { state } from "@/game.js";
import { formatTime } from "@/labels";

export default {
  name: "Project",
  components: { Scientific },

  data() {
    return {
      projects,
      selectedProject: null
    };
  },

  computed: {
    indexScientific() {
      return parseInt(this.$route.params.indexScientific);
    },
    canStartProject() {
      return (
        this.selectedProject &&
        !this.isProjectDone(this.selectedProject) &&
        hasRessources(this.requiredRessources) &&
        this.scientific.level >= this.selectedProject.level
      );
    },
    requiredRessources() {
      return calcRequiredRessources(this.selectedProject);
    },
    scientific() {
      return state.scientifics[this.indexScientific];
    },
    dureeProject() {
      return formatTime(
        this.selectedProject.duree *
          1000 *
          state.researchTimeFactor *
          state.speed
      );
    }
  },

  methods: {
    hasRessources,

    selectProject(project) {
      if (this.isProjectDone(project)) return;

      if (this.selectedProject) {
        this.selectedProject = null;
      } else {
        this.selectedProject = project;
      }
    },

    startProject() {
      takeRessources(this.selectedProject.ressources);
      startProject(this.scientific, this.selectedProject);
      this.$router.push("/scientifics");
    },

    isProjectDone(project) {
      return state.projectsDone.includes(project.name) && !project.infinite;
    }
  }
};
</script>

<style lang="postcss" scoped>
.ressources ul {
  display: inline-block;
  vertical-align: text-bottom;

  margin-left: 0.5em;

  li {
    display: inline-block;
    vertical-align: text-bottom;

    .icon {
      display: inline-block;
      width: 24px;
      height: 24px;
      background-size: 24px 24px;
      margin: 0 0.5em 0 0;
      vertical-align: middle;
    }
  }
}

.project {
  min-height: 96px;
  background-image: url("../assets/science.png");
  background-size: 100% 100%;
  text-align: left;
  padding: 4px 64px 12px 16px;
  position: relative;

  &:not(.done) {
    cursor: pointer;
    &:hover {
      filter: contrast(1.25) brightness(1.1);
    }
  }

  &.done {
    filter: contrast(0.85) brightness(0.5);
  }

  &.done::after {
    content: url("../assets/icon-tick.png");
    position: absolute;
    right: 16px;
    top: 24px;
  }

  p {
    line-height: 1;
    font-size: 0.8em;
    margin-bottom: 4px;
  }
}

.params {
  background-image: url("../assets/science.png");
  background-size: 100% 100%;
  padding: 4px 16px;
  min-height: 96px;

  .duree {
    margin-left: 1em;
    padding: 0.25em;
    display: inline-block;
  }

  .ressources {
    text-align: left;
  }
}

.insufficient {
  color: red;
}
</style>


